.column-3{
width: 75%;
float: left;
}
.column-2
{
    width: 50%;
    float: left;
}
.column-1{
    width: 25%;
    float: left;
}
button.calc-button{
    width: 100%;
    height: 100px;
    border: 2px solid #46B3E6;
    background: #ACE5FF;
    font-size: 32px;
    color: dimgrey
}
input.result{
    height: 100px;
    width: 100%;
    text-align: right;
    font-size: 32px;
    position: relative;
    max-width: 100vw;
    margin-right: 50px;
    float: right;
    border:none;
}
.floaty-last
{
    position: absolute;
    right: 50px;
    z-index: 99;
    top: 10px;
    color: black;
}